.info {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.name {
  display: flex;
  flex-direction: column;
  margin-top: 3rem;

  gap: 2rem;
}

.name img {
  margin-top: -30px;
  width: 200px;
}

.name h2 {
  font-size: 3rem;
  color: #5e8eff;
}

p {
  margin: 0;
}
span {
  font-weight: bold;
}

.summary {
  margin-top: -2rem;
  margin-bottom: -2rem;
  padding: 30px;
  padding-left: 100px;
  border-left: 1px solid #fff;
}

.img {
  display: flex;
  justify-content: center;
}

img {
  width: 300px;
}

.location {
  display: flex;
  height: 40px;

  align-items: center;
}

.icon {
  width: 30px;
}

.col {
  margin: 15px 0;
}
