.estudios-title {
  font-size: 2rem;
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  margin-top: -1rem;

  color: #5e8eff;
}

.estudios__container {
  display: flex;
  justify-content: space-between;
}

.estudios__listado {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.studying {
  display: flex;
  justify-content: center;

  align-items: center;

  margin-right: 5rem;
}
