.Skills-title {
  display: flex;
  justify-content: center;
  margin-top: -2rem;
  color: #5e8eff;
}

.Skills {
  width: 800px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 50px;
  margin-top: 2rem;
}

.Skill {
  margin: 0 auto;
}

.comenzando {
  position: fixed;
  margin-top: 55px;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 0.5rem;
  border-radius: 10px;
}
