.Modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

  background-color: rgba(0, 0, 0, 0.9);

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Modal__container {
  position: relative;
  width: auto;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4rem;
}

/* .img {
  background-color: #5e8eff;
  padding: 0.8rem;
  border-radius: 10px;
} */

.Modal__container-close {
  border: none;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  font-weight: bold;
  font-size: 3rem;
}

.Modal__container-close:hover {
  background-color: #5e8eff;
  color: #fff;
}
