.Proyecto {
  display: flex;
  height: 9rem;
  gap: 2rem;

  border: 2px solid #272822;
  border-radius: 5px;
}

.Proyecto:hover {
  background-color: rgba(0, 0, 0, 1);
  border: 2px solid #5e8eff;

  color: #fff;
}

.Proyecto img {
  border-radius: 5px;
}

.Proyecto__info-container {
  padding: 10px;
}

.Proyecto__info {
  width: 30rem;
}

.Poyecto__info-title {
  display: flex;
  gap: 1rem;
  color: #5e8eff;
}

.Proyecto__buttons {
  display: flex;
  flex-direction: column;
  margin: auto;
  gap: 20px;
}

.Proyecto__buttons button {
  background-color: #5e8eff;
  border: none;
  border-radius: 5px;
  height: 35px;

  font-weight: 600;
}

.Proyecto__buttons a:hover {
  background-color: #5e8eff;
}
