.nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  border-radius: 10px 0;
  background-color: rgb(0, 0, 0, 0.7);
}

.buttons {
  display: flex;
  justify-content: center;
  gap: 9rem;
}

.buttons a {
  width: auto;
  font-size: 1.5rem;
}

a {
  text-decoration: none;
  color: #fff;
}

a:hover {
  color: #5e8eff;
}
